import { defineStore } from 'pinia'
import request from 'utils/request'
import store from 'store'

export const useBuybackNotes = defineStore('buybackNotes', {
  state: () => ({
    routeParams: null,
    getNotes: {
      isLoading: false,
      internal: [],
      customer: []
    },
    formNotes: {
      mode: 'add',
      note: null,
      id: null,
      type: 'internal',
      tabs: [
        {
          name: 'Internal Note',
          component: 'internal',
          disabled: false
        },
        {
          name: 'Customer Note',
          component: 'customer',
          disabled: false
        }
      ]
    },
    addNotes: {
      modal: false,
      isLoading: false
    },
    editNotes: {
      item: undefined,
      modal: false,
      isLoading: false
    },
    deleteNotes: {
      item: undefined,
      modal: false,
      isLoading: false
    },
    warningNotes: {
      ids: [],
      page: 1,
      lastPage: 1,
      isReviewed: false,
      isLoading: false,
      modal: false,
      notes: []
    }
  }),
  getters: {},
  actions: {
    async $setRoute(payload = {}) {
      this.routeParams = payload
    },
    async $getNotes(payload = {}) {
      this.getNotes.isLoading = true

      try {
        const response = await request({
          url: `/v1/pos/order-notes/${this.routeParams?.buybackId}`,
          params: payload,
          method: 'get'
        })
        this.setNotesCustomer(response.customer, store.state.user.me.id)
        this.setNotesInternal(response.internal, store.state.user.me.id)
      } finally {
        this.getNotes.isLoading = false
      }
    },
    async $getNotesUnread() {
      this.warningNotes.isLoading = true
      this.warningNotes.isReviewed = false
      try {
        const response = await request({
          url: `/v1/pos/order-notes/unread`,
          params: {
            admin_id: store.state.user.me.id,
            order_ids: this.warningNotes.ids,
            page: this.warningNotes.page
          },
          method: 'get'
        })
    
        const currentAdminId = store.state.user.me.id
        const newNotes = Array.isArray(response.data)
          ? response.data.map(item => ({
              ...item,
              order_notes: item.order_notes.filter(note => note.admin_id !== currentAdminId)
            }))
          : []
    
        this.warningNotes.lastPage = response.last_page
        this.warningNotes.page = response.current_page
    
        if (this.warningNotes.page === 1) {
          // First page: replace notes
          this.warningNotes.notes = newNotes
        } else {
          // Subsequent pages: merge with existing notes by box_id
          const existingNotes = [...this.warningNotes.notes]
    
          newNotes.forEach(newItem => {
            const existingIndex = existingNotes.findIndex(e => e.box_id === newItem.box_id)
            if (existingIndex !== -1) {
              // Merge order_notes while avoiding duplicates (optional enhancement)
              const existingOrderNotes = existingNotes[existingIndex].order_notes
              const combinedNotes = [
                ...existingOrderNotes,
                ...newItem.order_notes.filter(
                  newNote => !existingOrderNotes.some(
                    existingNote => existingNote.id === newNote.id
                  )
                )
              ]
              existingNotes[existingIndex].order_notes = combinedNotes
            } else {
              // Push new
              existingNotes.push(newItem)
            }
          })
    
          this.warningNotes.notes = existingNotes
        }
      } catch (error) {
        console.error('Failed to fetch unread notes:', error)
      } finally {
        this.warningNotes.isLoading = false
      }
    },
    async $updateNotesUnread() {
      this.warningNotes.isLoading = true
      try {
        await request({
          url: `/v1/pos/order-notes/read-status`,
          data: {
            admin_id: store.state.user.me.id,
            order_ids: this.warningNotes.ids
          },
          method: 'post'
        })
        return true
      } catch (error) {
        console.error('Failed to update unread notes:', error)
        return false
      } finally {
        this.warningNotes.isLoading = false
      }
    },
    async $addNotes() {
      this.addNotes.isLoading = true
      try {
        await request({
          url: '/v1/pos/order-notes',
          data: {
            admin_id: store.state.user.me.id,
            box_id: this.routeParams?.buybackId,
            note: this.formNotes.note,
            type: this.formNotes.type
          },
          method: 'post'
        })
        await this.$getNotes()
      } finally {
        this.addNotes.isLoading = false
      }
    },
    async $editNotes() {
      this.editNotes.isLoading = true
      try {
        await request({
          url: `/v1/pos/order-notes/update/${this.formNotes.id}`,
          data: {
            admin_id: store.state.user.me.id,
            note: this.formNotes.note,
            _method: 'PUT'
          },
          method: 'post'
        })
        await this.$getNotes()
      } finally {
        this.editNotes.isLoading = false
      }
    },
    async $deleteNotes() {
      this.deleteNotes.isLoading = true
      try {
        await request({
          url: `/v1/pos/order-notes/delete/${this.formNotes.id}`,
          data: {
            _method: 'DELETE'
          },
          method: 'post'
        })
        await this.$getNotes()
      } finally {
        this.deleteNotes.isLoading = false
      }
    },
    setNotesCustomer(data, adminId) {
      this.getNotes.customer = data.map(i => ({
        ...i,
        type: 'customer',
        is_granted: adminId === i.admin_id
      })) || []
    },
    setNotesInternal(data, adminId) {
      this.getNotes.internal = data.map(i => ({
        ...i,
        type: 'internal',
        is_granted: adminId === i.admin_id
      })) || []
    },
    resetGetNotes() {
      this.getNotes.internal = []
      this.getNotes.customer = []
    },
    resetForm() {
      this.formNotes.mode = 'add'
      this.formNotes.note = null
      this.formNotes.id = null
      this.formNotes.type = 'internal'
      this.formNotes.tabs = this.formNotes.tabs.map(i => ({ ...i, disabled: false }))
    }
  }
})
